@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  height: 100vh;
  color: #100d53;
}

#root {
  width: 100%;
  height: 100%;
}

.wrap {
  max-width: 1440px;
  width: 100%;
  margin: auto;
}

.primary-color {
  color: #e25e70 !important;
}

.text-color {
  color: #100d53;
}

#app {
  width: 100%;
  height: 100%;
}
#landing {
  width: 100%;
  height: 100%;
}
#landingSplash {
  width: 100%;
  height: 900px;
  background-color: rgba(0, 0, 0, 0.85);
  display: flex;
  align-items: center;
  justify-content: center; }
  #landingSplash img {
    width: 100%; }
  #landingSplash h1 {
    font-size: 100px;
    color: white; }

#why-section {
  padding: 120px 82px; }
  #why-section .wrap {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around; }
  #why-section #header {
    max-width: 445px;
    font-size: 86px;
    font-weight: 900;
    margin: 0; }
  #why-section .section {
    display: flex;
    flex-direction: column;
    height: 100%; }
  #why-section h2 {
    font-size: 100px;
    font-weight: bold;
    margin: 5px 0;
    color: #100d53; }
  #why-section h6 {
    font-size: 40px;
    font-weight: 500;
    color: #100d53;
    margin: 0; }
  #why-section .box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 80px; }

#what-section {
  background-color: #f6f7fb;
  padding: 100px; }
  #what-section #header {
    font-size: 86px;
    text-align: center;
    margin-bottom: 164px; }
  #what-section .section {
    display: flex;
    flex-direction: row;
    justify-content: space-around; }
  #what-section .box {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 554px;
    text-align: center; }
  #what-section .reduceInterventions {
    font-size: 44px;
    font-weight: 500;
    max-width: 320px;
    margin: 0; }
  #what-section .saveLives {
    font-size: 50px;
    font-weight: bold;
    max-width: 240px;
    margin: 0; }
  #what-section .reduceCosts {
    font-size: 44px;
    font-weight: 500;
    max-width: 323px;
    margin: 0; }

#how-section {
  padding: 100px 151px; }
  #how-section #header {
    font-size: 86px;
    text-align: center;
    margin-bottom: 156px; }
  #how-section .box {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-bottom: 231px; }
    #how-section .box img {
      max-width: 660px;
      width: 100%; }
    #how-section .box:last-child {
      margin-bottom: 0; }
  #how-section .text {
    max-width: 480px;
    font-size: 30px; }
  #how-section h6 {
    font-size: 36px;
    margin: 0;
    margin-bottom: 20px; }
  #how-section .wearableImg {
    margin-right: 96px; }
  #how-section .aiCloudTechImg {
    margin-left: 37px; }
  #how-section .triageImg {
    margin-right: 96px; }

#submit-section {
  background-color: #100d53;
  padding: 80px; }
  #submit-section .wrap {
    display: flex;
    flex-direction: column;
    max-width: 616px;
    align-items: center;
    justify-content: center; }
  #submit-section #header {
    margin-bottom: 83px; }
  #submit-section h1 {
    font-size: 44px;
    font-weight: 500;
    width: 505px;
    margin: 0;
    margin-bottom: 28px;
    text-align: center; }
    #submit-section h1:last-child {
      margin-bottom: 0; }
  #submit-section form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%; }
    #submit-section form input {
      background-color: transparent;
      border: none;
      font-size: 20px;
      color: white;
      border-bottom: 1px solid white;
      margin-bottom: 68px;
      width: 100%;
      padding: 10px 0; }
      #submit-section form input::-webkit-input-placeholder {
        color: white; }
      #submit-section form input::-ms-input-placeholder {
        color: white; }
      #submit-section form input::placeholder {
        color: white; }
    #submit-section form button {
      background-color: #e25e70;
      color: #100d53;
      width: 183px;
      padding: 12px;
      border-radius: 6px;
      border: none;
      font-size: 20px;
      font-weight: bold; }
      #submit-section form button:disabled {
        color: black;
        background-color: gray; }

